.hero_cont {
  width: 100%;
  display: flex;
  overflow-x: hidden;
}

.hero {
  width: 100%;
  padding-bottom: 30px;
  position: relative;
}

.hero .hero_image {
  height: 550px;
  display: flex;
}

.hero .hero_image .image_group {
  width: 100vw;
  height: 550px;
}

.hero .hero_image .image_group img {
  width: 100vw;
  height: 100%;
  object-fit: cover;
}

.hero .hero_text {
  position: absolute;
  top: 11%;
  text-align: center;
  display: flex;
  width: 300%;
}

.hero .hero_text .text_group {
  width: 100vw;
  position: absolute;
  text-align: center;
  padding-left: 30px;
  padding-right: 30px;
  top: 11%;
  left: 200vw;
  opacity: 0;
  transition: all 0.5s ease-in;
}

.hero .hero_text .text_group.active {
  left: 0vw;
  opacity: 1;
}

.hero .hero_text .text_group h2 {
  color: #fff;
  font-size: 1.7rem;
  text-transform: uppercase;
  line-height: 40px;
  /* max-width: 300px; */
  margin: auto;
  margin-bottom: 15px;
  letter-spacing: 2px;
  /* margin: 5px; */
  font-weight: 600;
}

.hero .hero_text .text_group p {
  color: #fff;
  max-width: 270px;
  margin: auto;
  line-height: 30px;
  text-transform: uppercase;
  font-weight: 500;
}

.hero .hero_text .hero_btn {
  width: 100vw;
  cursor: pointer;
  position: absolute;
  top: 365px;
  display: flex;
  justify-content: center;
}

.hero .hero_controls {
  position: absolute;
  top: 50%;
  display: flex;
  padding-left: 20px;
  padding-right: 20px;
  width: 100%;
  justify-content: space-between;
}

.hero .hero_controls .hero_prev,
.hero .hero_controls .hero_next {
  cursor: pointer;
}

.hero .hero_dot {
  position: absolute;
  bottom: 10%;
  display: flex;
  justify-content: center;
  width: 100%;
  margin: auto;
}

.hero .hero_dot .dot {
  width: 10px;
  height: 10px;
  background-color: #fff;
  border-radius: 50%;
  cursor: pointer;
  margin: 4px;
}

.hero .hero_dot .dot.active {
  background-color: blue;
}


@media only screen and (min-width: 550px) {
  .hero .hero_text {
    top: 15%; 
  }

  .hero .hero_text .text_group h2 {
    font-size: 2.5rem;
    line-height: 55px;
  }

  .hero .hero_text .text_group p {
    max-width: 360px;
  }

  .hero .hero_text .hero_btn {
    top: 355px;
    
  }
}


